.anonymous {
  margin-bottom: 100px;

  &__title {}

  &__inner {
    background: linear-gradient(180deg, #222124 0%, #262628 100%);
    border-radius: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 87px;
    padding-bottom: 87px;
    position: relative;
    overflow: hidden; }

  &__picture {
    width: 45%;
    position: absolute;
    left: 0;
    bottom: 0; }

  &__img-wrap {
    width: 100%;
    padding-top: 52%;
    position: relative;
    left: 0; }

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }

  &__text {
    font-weight: 600;
    color: #FFFFFF;
    width: 233px;
    text-align: center;
    margin-right: 6.5%;
    z-index: 1;

    @include mw(1200) {
      margin-right: 30px; } }

  &__cont {
    margin-right: 6.5%;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include mw(1200) {
      margin-right: 30px; } }

  &__cont-text {
    font-weight: 300;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    color: #B0B3C6;
    width: 179px;
    margin-bottom: 10px; }

  &__link {
    font-weight: 500;
    color: #B0B3C6;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 15px;
    display: block;
    border: 1px solid #3D3E48;
    border-radius: 4px;
    display: flex;
    align-items: center;

    &:hover {
      opacity: 0.5; }

    & .icon {
      display: block;
      margin-left: 10px;

      & svg {
        fill: transparent;
        stroke: #B0B3C6;
        transform: translateY(3px); } } } }

.container {}

.size-3 {}
